import http from "../http-common";

class QnaDataService {
    // Î™®Îì† Í≥†Í∞ùÏ†ïÎ≥¥ Ï°∞Ìöå ÏöîÏ≤≠ Ìï®Ïàò + Í∏ÄÏ†úÎ™© + Í∏ÄÎÇ¥Ïö© Í≤ÄÏÉâ ÏöîÏ≤≠ Ìï®Ïàò 
    // searchSelect : Í∏ÄÏ†úÎ™©Í≥º ÏßàÎ¨∏Ïûê Í≤ÄÏÉâ(Ï≤¥ÌÅ¨Î∞ïÏä§Î°úÏÑ†ÌÉù)
    // page : ÌòÑÏû¨ ÌéòÏù¥ÏßÄ Î≤àÌò∏ 
    // size : Ìïú ÌéòÏù¥ÏßÄÎãπ ÌôîÎ©¥Ïóê ÌëúÏãúÌï† Îç∞Ïù¥ÌÑ∞ Í∞ØÏàò
    getAll(searchSelect, searchKeyword, page, size) {
    //   alert("ÌôïÏù∏Ïö©2")

        // get Î∞©Ïãù ÌÜµÏã† ÏöîÏ≤≠ -> @GetMapping("/api/qna")
        // 1) Ï†ÑÏ≤¥ Ï°∞Ìöå ÏôÄ
        // 2) ÏßàÎ¨∏ Ï°∞ÌöåÎ•º Í∞ôÏù¥ ÌïòÎäî Ìï®Ïàò getAll
        return http.get(`/qna?searchSelect=${searchSelect}&searchKeyword=${searchKeyword}&page=${page}&size=${size}`); 
    }

    // CIDÎ°ú Ï°∞Ìöå ÏöîÏ≤≠ Ìï®Ïàò
    // get Î∞©Ïãù ÌÜµÏã† ÏöîÏ≤≠ -> @GetMapping("/api/qna/{qno}"), @PathVariable
    get(qno) {
        return http.get(`/qna/${qno}`)
    }

    // Í≥†Í∞ùÏ†ïÎ≥¥ ÏÉùÏÑ±(insert) ÏöîÏ≤≠ Ìï®Ïàò
    // post Î∞©Ïãù ÌÜµÏã† ÏöîÏ≤≠ -> @PostMapping("/api/qna"), @RequestBody
    create(data) {
        console.log(data);
        return http.post("/qna", data);
    }

    // Í≥†Í∞ùÏ†ïÎ≥¥ ÏàòÏ†ï(update) ÏöîÏ≤≠ Ìï®Ïàò
    // put Î∞©Ïãù ÌÜµÏã† ÏöîÏ≤≠ -> @PutMapping("/api/qna/{qno}"), @RequestBody
    update(qno, data) {
        return http.put(`/qna/${qno}`, data);
    }

    // Í≥†Í∞ùÏ†ïÎ≥¥ ÏÇ≠Ï†ú(delete) ÏöîÏ≤≠ Ìï®Ïàò
    // delete Î∞©Ïãù ÌÜµÏã† ÏöîÏ≤≠ -> @DeleteMapping("/api/qna/deletion/{qno}")
    //                        , @PathVariable  
    delete(qno) {
        return http.delete(`/qna/deletion/${qno}`);
    }

    // // Í≥†Í∞ùÏ†ïÎ≥¥ Ï†ÑÏ≤¥ ÏÇ≠Ï†ú ÏöîÏ≤≠ Ìï®Ïàò
    // // delete Î∞©Ïãù ÌÜµÏã† ÏöîÏ≤≠ -> @DeleteMapping("/api/qna/all")
    // deleteAll() {
    //     return http.delete("/qna/all")
    // }

}

export default new QnaDataService;