<template>
  <div>
    <!-- search Í¥ÄÎ†® div ÏãúÏûë -->
    <div class="col-md-8">
      <div class="input-group mb-3">
        <div class="col-2">
          <!-- Í≤ÄÏÉâ select Î∞ïÏä§ :Í∏ÄÏ†úÎ™©(question)ÏúºÎ°ú Í≤ÄÏÉâ ÎòêÎäî ÏûëÏÑ±Ïûê(questioner)Î°ú Í≤ÄÏÉâ -->
          <select class="form-select" v-model="searchSelect">
            <option>question</option>
            <option>questioner</option>
          </select>
        </div>

        <!-- searchDname -> searchQuestion -> searchKeyword Î≥ÄÍ≤Ω -->
        <div class="col-9">
          <input
            type="text"
            class="form-control"
            placeholder="Search by Question"
            v-model="searchKeyword"
          />
        </div>

        <div class="input-group-append col-1">
          <button
            class="btn btn-outline-secondary"
            type="button"
            @click="
              page = 1;
              retrieveQna();
            "
          >
            Search
          </button>
        </div>
        <!--            Todo : ÏàòÏ†ï ÎÅù -->
      </div>
    </div>
    <!-- search Í¥ÄÎ†® div ÎÅù -->

    <!--    Todo : page Î∞î ÏãúÏûë -->
    <div class="col-md-12">
      <div class="mb-3">
        Items per Page:
        <select v-model="pageSize" @change="handlePageSizeChange($event)">
          <option v-for="size in pageSizes" :key="size" :value="size">
            <!-- <!‚Äî            size : 3, 6, 9 ‚Äî> -->
            {{ size }}
          </option>
        </select>
      </div>

      <b-pagination
        v-model="page"
        :total-rows="count"
        :per-page="pageSize"
        prev-text="Prev"
        next-text="Next"
        @change="handlePageChange"
      ></b-pagination>
    </div>
    <!-- <!‚Äî    Todo : page Î∞î ÎÅù ‚Äî> -->

    <table class="table">
      <thead>
        <tr>
          <th scope="col">Qno</th>
          <th scope="col">Question</th>
          <th scope="col">Questioner</th>
          <th scope="col">Answer</th>
          <th scope="col">Answerer</th>
        </tr>
      </thead>
      <tbody v-for="(data, index) in qna" :key="index">
        <tr>
          <td>{{ data.qno }}</td>
          <td>{{ data.question }}</td>
          <td>{{ data.questioner }}</td>
          <td>{{ data.answer }}</td>
          <td>{{ data.answerer }}</td>
          <td>
            <router-link :to="'/qna/' + data.qno"
              ><span class="badge bg-success">Edit</span></router-link
            >
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>  
  
<script>
// üëÄaxios Í≥µÌÜµÌï®Ïàò(springboot Ïó∞ÎèôÏùÑ ÏúÑÌïú Ìï®ÏàòÎì§Ïùò Î™®ÏûÑ) import ÌïÑÏàò!!
import QnaDataService from "../../services/QnaDataService";

  export default {
    data() {
      return {
      qna: [],
      // currentQna: null,
      // currentIndex: -1,
      // dname: "", ÏÜåÏä§ Î≥ÄÍ≤Ω-> searchDname: "" ÏùÑ searchKeyword, searchSelect ÏúºÎ°ú Î≥ÄÍ≤Ω
      searchKeyword: "",  // Í≤ÄÏÉâÏñ¥
      searchSelect: "question",  // ÏÖÄÎ†âÌä∏Î∞ïÏä§ Í∏∞Î≥∏Í∞í (ÏÑ†ÌÉùÏïàÌñàÏùÑÎïå_Í∏ÄÏ†úÎ™©questionÏúºÎ°ú Í≤ÄÏÉâ) 

      // ÌéòÏù¥ÏßïÏùÑ ÏúÑÌïú Î≥ÄÏàò Ï†ïÏùò(ÏµúÏ¥àÏÑ∏ÌåÖ) 4Í∞ÄÏßÄ
      page: 1,      // ÌòÑÏû¨ ÌéòÏù¥ÏßÄ
      count: 0,     // Ï†ÑÏ≤¥ Îç∞Ïù¥ÌÑ∞ Í±¥Ïàò
      pageSize: 3,  // Ìïú ÌéòÏù¥ÏßÄÎãπ ÌôîÎ©¥Ïóê Î≥¥Ïó¨Ï§Ñ Í∞úÏàòÎ•º Í≤∞Ï†ïÌïòÎäî Î≥ÄÏàò

      pageSizes: [3, 6, 9]  // ÏÖÄÎ†âÌä∏Î∞ïÏä§Ïóê ÎÑ£ÏùÑ Í∏∞Î≥∏ Îç∞Ïù¥ÌÑ∞. ÏÑ†ÌÉùÏ∞Ω
      };
    },
    methods: {
      // Ìï®ÏàòÎ™Ö : retrieveQna()
      // Îß§Í∞úÎ≥ÄÏàò : this.searchKeyword (Í≤ÄÏÉâÏñ¥), this.searchSelect(ÏÖÄÎ†âÌä∏Î∞ïÏä§ ÏÑ†ÌÉùÍ∞í)
      //           this.page-1 (ÌòÑÏû¨ ÌéòÏù¥ÏßÄ Î≤àÌò∏)
      //           this.pageSize (ÌéòÏù¥ÏßÄÎãπ Ï∂úÎ†•Ìï† Îç∞Ïù¥ÌÑ∞Í∞ØÏàò)
      // Î¶¨ÌÑ¥Í∞í : ÏóÜÏùå
      retrieveQna() {
        QnaDataService.getAll(
          this.searchSelect,   // ÏÖÄÎ†âÌä∏Î∞ïÏä§ÏóêÏÑú ÏÑ†ÌÉùÎêú Í∞í
          this.searchKeyword,  // Í≤ÄÏÉâÏñ¥
          this.page-1, 
          this.pageSize)  // ÌîÑÏóî ÏûêÎèô ÌéòÏù¥ÏßïÏ≤òÎ¶¨Í∞Ä 1Î∂ÄÌÑ∞ ÏãúÏûëÌïòÎØÄÎ°ú -1Ìï¥ÏÑú 0ÏúºÎ°ú ÎßåÎì§Ïñ¥ Ï§å(Ïä§ÌîÑÎßÅÎ∂ÄÌä∏Îäî 0Î≤àÎ∂ÄÌÑ∞ Îß§Í∏∞ÎãàÍπå ÎßûÏ∂∞Ï§å)
        // axiosÏÑ±Í≥µÌïòÎ©¥ Ïä§ÌîÑÎßÅÎ∂ÄÌä∏ Í≤∞Í≥ºÍ∞Ä .then() ÏïàÏúºÎ°ú Ï†ÑÏÜ°Îê®
        .then((response) => {
          // const(let){ÏÜçÏÑ±Î™Ö, ÏÜçÏÑ±Î™Ö2} = Îç∞Ïù¥ÌÑ∞Í∞ùÏ≤¥Î∞∞Ïó¥  : Î™®ÎçòÏûêÎ∞îÏä§ÌÅ¨Î¶ΩÌä∏ Î¨∏Î≤ï Íµ¨Ï°∞Î∂ÑÌï¥Ìï†Îãπ
          const {qna, totalItems} = response.data; // springboot ÏóêÏÑú Ï†ÑÏÜ°Îêú Îßµ(map) Ï†ïÎ≥¥
          this.qna = qna;         // Ïä§ÌîÑÎßÅ Î∂ÄÌä∏ÏóêÏÑú Ï†ÑÏÜ°Ìïú Îç∞Ïù¥ÌÑ∞
          this.count = totalItems;  // Ïä§ÌîÑÎßÅÎ∂ÄÌä∏ÏóêÏÑú Ï†ÑÏÜ°Ìïú ÌéòÏù¥ÏßÄÏ†ïÎ≥¥(Ï¥ù Í±¥Ïàò)
          // ÎîîÎ≤ÑÍπÖ ÏΩòÏÜîÏóê Ï†ïÎ≥¥ Ï∂úÎ†•
          console.log(response.data);
        })
        // Ïã§Ìå®ÌïòÎ©¥ .catch() Ïóê ÏóêÎü¨Í∞Ä Ï†ÑÏÜ°Îê®
        .catch((e) => {
          console.log(e);
        });
      },
      // select box Í∞í Î≥ÄÍ≤ΩÏãú Ïã§ÌñâÎêòÎäî Ìï®Ïàò(Ïû¨Ï°∞Ìöå)üí´
      // event : ÏÇ¨Ïö©ÏûêÍ∞Ä ÌÅ¥Î¶≠Ìïú Ïª®Ìä∏Î°§Ïù¥ Ïù¥Î≤§Ìä∏Í∞ùÏ≤¥Î•º ÌÜµÌï¥ Ï†ëÍ∑º,ÌôïÏù∏Í∞ÄÎä•
      handlePageSizeChange(event) {
        this.pageSize = event.target.value    // ÏÖÄÎ†âÌä∏Î∞ïÏä§ Í∞í Í∞ÄÏ†∏Ïò§Í∏∞ -> Ìïú ÌéòÏù¥ÏßÄÎãπ Ï∂úÎ†•Ìï† Îç∞Ïù¥ÌÑ∞ Í∞ØÏàò Ï†ÄÏû•
        this.page = 1;    // ÌòÑÏû¨ÌéòÏù¥ÏßÄÎ•º Ï≤´ ÌéòÏù¥ÏßÄÎ°ú ÏÑ∏ÌåÖ
        // Ïû¨Ï°∞ÌöåÌï®Ïàò Ìò∏Ï∂ú
        this.retrieveQna();
      },
      // ÌéòÏù¥ÏßÄÎ≤àÌò∏ Î≥ÄÍ≤Ω Ïãú Ïã§ÌñâÎêòÎäî Ìï®Ïàò(Ïû¨Ï°∞Ìöå)üî±
      handlePageChange(value) {
        // Îß§Í∞úÎ≥ÄÏàòÍ∞íÏúºÎ°ú ÌòÑÏû¨ ÌéòÏù¥ÏßÄÎ≤àÌò∏ Î≥ÄÍ≤Ω
        this.page = value;
        // Ïû¨Ï°∞Ìöå Ìï®Ïàò Ìò∏Ï∂ú
        this.retrieveQna();
      }
    },
    // üëÄÌôîÎ©¥Ïù¥ Îú®ÏûêÎßàÏûê Ïã§ÌñâÎêòÎäî Ïù¥Î≤§Ìä∏(ÎùºÏù¥ÌîÑ ÏÇ¨Ïù¥ÌÅ¥ Ìï®Ïàò) : mounted(), created()
    mounted() {
        this.retrieveQna(); // üëÄÌôîÎ©¥ Î°úÎî©Ïãú Ï†ÑÏ≤¥ Ï°∞ÌöåÌï®Ïàò retrieveQna() Ìï®Ïàò Ïã§Ìñâ
    },
  
  }
</script>
  
